@import url("https://fonts.googleapis.com/css2?family=Mina:wght@400;700&display=swap");
:root {
  --minecraft-font: "Minecraft";
  --mina-regular-font: Mina;
  --mina-weight-regular: 400;
  --mina-weight-bold: 700;

  --background-color: #282c34;
  --text-color: rgb(0, 0, 0);
  --text-active-color: #ffffff;
  --link-color: #61dafb;
  --button-color: #ff0000;
  --button-hover-color: #ffe2e2;
  --button-active-color: #b72020;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: "Minecraft";
  src: url("./fonts/Minecraft.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "MinaR";
  src: url("./fonts/Mina-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "MinaB";
  src: url("./fonts/Mina-Bold.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: "Minecraft";
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background-color: #b32024; /* Red background */
}
hr {
  border: 0;
  height: 1px;
  background: #333;
  margin-top: 15px;
  margin-bottom: 10px;
}
.announcement-container {
  background-color: #f8f8f8;
  max-width: 500px;
  min-width: 400px;
  height: auto;
  width: 100%;

  padding-inline: 40px;
  padding-top: 20px;
  padding-bottom: 40px;
  border-radius: 15px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  border: 5px solid #333;
}
.announcement-logo {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.announcement-logo img {
  width: 140px;
  height: 60px;
}
.announcement-logo .text-wrapper {
  position: absolute;
  top: 25px; /* Adjust to control the overlap with the logo */
  background-color: rgb(255, 255, 255); /* Semi-transparent background */
  font-size: 21px;
}
.announcement-logo .text {
  font-size: 20px;
  color: #333;
}
.message {
  font-family: MinaR;
  font-weight: 900;
  font-size: 17px;
  color: #333;
  margin: 20px;
}
.sub-message {
  font-family: MinaB;
  font-size: 18px;
  font-weight: bold;
  color: #333;
  margin-bottom: 12px;
}
.email-input {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #333;
  border-radius: 5px;
  overflow: hidden;
}
.email-input input[type="email"] {
  width: 100%;
  padding: 10px;
  border: none;
  outline: none;
  font-size: 16px;
  font-family: Minecraft;
}
.button-search {
  background-color: #ff0000;
  color: white;
  border: 2px solid #000000;
  cursor: pointer;
  font-family: Minecraft;
  width: 60px; /* Adjusted width */
  height: 40px; /* Adjusted height */
  font-size: 26px; /* Adjusted font size */
  padding-top: 7px;
  display: flex;
  justify-content: center;
  align-items: center;

  transition: transform 0.05s ease, box-shadow 0.05s ease;
  box-shadow: 2px 1px 0px 1px rgb(0, 0, 0);
}
.button-search:hover,
.button-back:hover {
  transform: translate(-3px, -3px);
  box-shadow: 5px 4px 0px 1px rgb(0, 0, 0);
  /* outline: 5px solid #ffffff; */
  background-color: #ffffff;
  color: #000000;
}

.button-search:active,
.button-back:active {
  transform: translate(0px, 0px);
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
  box-shadow: 2px 1px 0px 1px rgb(0, 0, 0);
}
